:host {
  width: 100%;
  height: 100vh;
}

#login-component {
  .card {
    height: 100%;
    width: 100%;
    background: rgb(255 253 253 / 93%);
    border-radius: 0px;
    display: flex;
    align-items: center;
  }

  .card-title {
    color: #000;
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    width: 100%;
    margin-bottom: 12px;
  }

  .screen-card {
    display: flex;
    flex-direction: column;
    flex: 1 1 auto;
    align-items: center;
    justify-content: center;
    max-width: 300px;
    width: 300px;
  }

  .form-group {
    margin-bottom: 0 !important;
  }

  .input-bottom-section {
    display: flex;
    justify-content: space-between;
    width: 100%;

    .checkbox-div {
      display: flex;
      gap: 8px;
      align-items: center;

      input {
        border-radius: 5px;
        border: 1px solid #9d9fa2;
        background: #fff;
        width: 20px;
        height: 20px;
      }

      .checkbox-text {
        color: #000;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
      }
    }

    .forgot-password-text {
      a {
        color: #0f72ad;
        text-align: right;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        cursor: pointer;
      }
    }
  }

  .login-div {
    width: 100%;
    padding-top: 35px;

    button {
      width: 100%;
      font-size: 16px;
      font-weight: 500;
      height: 40px;

      &:hover {
        color: white;
      }
    }
  }

  .vision-text {
    color: #fff;
    font-size: 48px;
    font-weight: 700;
    left: 55px;
    bottom: 106px;
    line-height: normal;

    span {
      color: #00aeef;
    }
  }

  .term-of-services {
    bottom: 5%;
    position: absolute;
    color: #142430;
    text-align: center;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    padding: 0 5%;

    a {
      text-decoration-line: underline;
      cursor: pointer;
    }

    .version-text {
      padding-top: 16px;
    }
  }
}

#mfa-qr-code-component {
  .mfr-code-card {
    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    display: flex;
    flex-direction: column;
    border-radius: 8px;
    background: rgba(255, 255, 255, 0.93);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    padding: 52px 60px 27px 60px;
    max-width: 420px;
    width: 420px;

    .logo {
      display: flex;
      justify-content: center;

      img {
        width: 176px;
        height: 40px;
      }
    }

    .mfr-title {
      color: #000;
      font-size: 20px;
      font-style: normal;
      font-weight: 700;
      line-height: normal;
      padding-top: 40px;
    }

    .qrcode-row {
      display: flex;
      justify-content: center;
      align-items: center;
      padding-top: 14px;
      gap: 16px;

      .qr-div {
        img {
          width: 133px;
          height: 130px;
        }
      }

      .qr-description {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 150px;
        gap: 20px;

        .description {
          color: #000;
          font-size: 14px;
          font-weight: 400;
          line-height: normal;
        }

        .number {
          color: #000;
          font-size: 14px;
          font-weight: 700;
        }
      }
    }

    .input-heading {
      padding-top: 38px;
      color: #000;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
    }

    .form-group {
      padding-top: 20px;
      margin-bottom: 0;

      mat-form-field {
        width: 100%;
      }
    }

    .checkbox-div {
      display: flex;
      gap: 8px;
      align-items: center;

      input {
        border-radius: 5px;
        border: 1px solid #9d9fa2;
        background: #fff;
        width: 20px;
        height: 20px;
      }

      .checkbox-text {
        color: #000;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
      }
    }

    .login-btn {
      padding-top: 20px;

      button {
        border-radius: 45px;
        background: #142430;
        width: 100%;
        border: none;
        height: 40px;
      }
    }

    .anchor-link {
      padding-top: 26px;
      display: flex;
      justify-content: center;

      a {
        color: #0f72ad;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        cursor: pointer;
      }
    }
  }

  .term-of-services {
    bottom: 0%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    color: var(--basic-white, #fff);
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;

    a {
      text-decoration-line: underline;
      cursor: pointer;
    }

    .version-text {
      padding-top: 10px;
    }
  }
}

